import {
  follow, requestUsers, setCurrentPage, unfollow,
} from '../../Redux/users-reducer'
import { connect } from 'react-redux'
import React from 'react'
import Users from './Users'
import Preloader from '../Common/Preloader/Preloader'
import { getCount, getCurrentPage, getFetching, getFollowingInProgress, getPageSize, getPortionSize, getUsers } from '../../Redux/usersSelectors.js'


class UsersContainer extends React.Component {
  
  componentDidMount() {
    const { currentPage, pageSize } = this.props
    this.props.getUsers(currentPage, pageSize)
  }
  
  onPageChanged = (pageNumber) => {
    const { pageSize } = this.props
    this.props.setCurrentPage(pageNumber)
    this.props.getUsers(pageNumber, pageSize)
  }
  
  userFollow = (id) => {
    const {follow}=this.props
    follow(id)
  }
  
  userUnfollow = (id) => {
    const {unfollow}=this.props
    unfollow(id)
  }
  
  render() {
    return <>
      { this.props[ 'isFetching' ]
          ? <Preloader />
          : <Users { ...this.props }
              userFollow = { this.userFollow }
              userUnfollow = { this.userUnfollow }
              onPageChanged = { this.onPageChanged }
          /> }
    </>
  }
}

let mapStateToProps = (state) => {
  return {
    users: getUsers(state),
    pageSize: getPageSize(state),
    count: getCount(state),
    portionSize:getPortionSize(state),
    currentPage: getCurrentPage(state),
    isFetching: getFetching(state),
    followingInProgress: getFollowingInProgress(state),
    
  }
}

export default connect(mapStateToProps, {
  follow,
  unfollow,
  setCurrentPage,
  getUsers: requestUsers,
})(UsersContainer)

